export default {
  name: 'MyProfile',
  props: {
    message: String
  },
  data () {
    return {
      position: 0,
      elements: [{image:'temoin.png', message:"Ramav production est une entreprise audio visuelle et prestation de servive, cre en 2016 et situ au coeur de la ville de Yaound au Cameroun, avec des reprsentations dans les autres villes du pays. L'entire satisfaction du client est notre objectif. De ce fait toutes nos actions sont rsolument... "},
        {image:'images.jpeg',message:'Ramav fourni des services extraordinares, vraiment satisfait du rsultat'},
        {image:'temoin.png',message:'Ramav is an oustanding .....'}
      ]
    }
  },
  methods: {
    move (i) {
      if ((this.position >= -100 && i === -1) || (this.position < 0 && i === 1)) {
        this.position += i * 100
      }
      let index = Math.abs(this.position)/100
      this.$emit('message',this.elements[index].message)
    }
  },
  computed: {
    Position () {
      return this.position + '%'
    }
  },
  mounted () {
    this.$emit('message',this.elements[0].message)
  }
}
